.post {
    color: var(--doc-font-color);
    font-size: var(--doc-font-size);
    hyphens: auto;
    line-height: var(--doc-line-height);
    margin: var(--doc-margin);
    max-width: var(--doc-max-width);
    padding: 0 1rem 4rem;
  }
  
  @media screen and (min-width: 1024px) {
    .post {
      flex: auto;
      font-size: var(--doc-font-size--desktop);
      margin: var(--doc-margin--desktop);
      max-width: var(--doc-max-width--desktop);
      min-width: 0; /* min-width: 0 required for flexbox to constrain overflowing elements */
      position: relative;
      z-index: var(--z-index-doc);
    }
  }
  
  .post h1,
  .post h2,
  .post h3,
  .post h4,
  .post h5,
  .post h6 {
    color: var(--heading-font-color);
    font-weight: var(--heading-font-weight);
    hyphens: none;
    line-height: 1.3;
    margin: 1rem 0 0;
  }
  
  .post > h1.page:first-child {
    font-size: calc(36 / var(--rem-base) * 1rem);
    margin: 1.5rem 0;
  }
  
  @media screen and (min-width: 769px) {
    .post > h1.page:first-child {
      margin-top: 2.5rem;
    }
  }
  
  .post > h1.page:first-child + aside.toc.embedded {
    margin-top: -0.5rem;
  }
  
  .post > h2#name + .sectionbody {
    margin-top: 1rem;
  }
  
  #preamble + .sect1,
  .post .sect1 + .sect1 {
    margin-top: 2rem;
  }
  
  .post h1.sect0 {
    background: var(--abstract-background);
    font-size: 1.8em;
    margin: 1.5rem -1rem 0;
    padding: 0.5rem 1rem;
  }
  
  .post h2:not(.discrete) {
    border-bottom: 1px solid var(--section-divider-color);
    margin-left: -1rem;
    margin-right: -1rem;
    padding: 0.4rem 1rem 0.1rem;
  }
  
  .post h3:not(.discrete) {
    font-weight: var(--alt-heading-font-weight);
  }
  
  .post h1 .anchor,
  .post h2 .anchor,
  .post h3 .anchor,
  .post h4 .anchor,
  .post h5 .anchor,
  .post h6 .anchor {
    position: absolute;
    text-decoration: none;
    width: 1.75ex;
    margin-left: -1.5ex;
    visibility: hidden;
    font-size: 0.8em;
    font-weight: normal;
    padding-top: 0.05em;
  }
  
  .post h1 .anchor::before,
  .post h2 .anchor::before,
  .post h3 .anchor::before,
  .post h4 .anchor::before,
  .post h5 .anchor::before,
  .post h6 .anchor::before {
    content: "\00a7";
  }
  
  .post h1:hover .anchor,
  .post h2:hover .anchor,
  .post h3:hover .anchor,
  .post h4:hover .anchor,
  .post h5:hover .anchor,
  .post h6:hover .anchor {
    color: var(--color-brand-primary);
    visibility: visible;
  }
  
  .post p,
  .post dl {
    margin: 0;
  }
  
  .post a {
    color: var(--link-font-color);
    background: linear-gradient(transparent 80%,rgba(51, 143, 229, 0.5) 81%,rgba(51, 134, 229, 0.5));
    text-decoration: none;
  }
  
  .post a:hover {
    color: var(--link_hover-font-color);
    background: linear-gradient(transparent 60%,rgba(51, 143, 229, 0.5) 61%,rgba(51, 134, 229, 0.5));
    text-decoration: none;
  }
  
  .post a:hover,
  .post a:hover > * {
    /* text-decoration: underline solid currentColor; */
  }
  
  .post a.bare {
    hyphens: none;
  }
  
  .post a.unresolved {
    color: var(--link_unresolved-font-color);
  }
  
  .post a.unresolved:hover {
    color: var(--link_unresolved-hover-font-color);
  }
  
  .post i.fa {
    display: inline-block;
    background: no-repeat center / 1em 1em;
    filter: var(--doc-icon-filter);
    hyphens: none;
    font-style: normal;
    height: 1em;
    width: 1em;
    vertical-align: -0.125em;
  }
  
  .post p code,
  .post thead code {
    color: var(--code-font-color);
    background: var(--code-background);
    border-radius: 0.25em;
    font-size: 0.95em;
    padding: 0.125em 0.25em;
  }
  
  .post code,
  .post pre {
    hyphens: none;
  }
  
  .post pre {
    font-size: calc(16 / var(--rem-base) * 1rem);
    line-height: 1.5;
    margin: 0;
  }
  
  .post blockquote {
    margin: 0;
  }
  
  .post .paragraph.lead > p {
    font-size: calc(18 / var(--rem-base) * 1rem);
  }
  
  .post .right {
    float: right;
  }
  
  .post .left {
    float: left;
  }
  
  .post .stretch {
    width: 100%;
  }
  
  .post .underline {
    text-decoration: underline;
  }
  
  .post .line-through {
    text-decoration: line-through;
  }
  
  .post .paragraph,
  .post .dlist,
  .post .hdlist,
  .post .olist,
  .post .ulist,
  .post .exampleblock,
  .post .imageblock,
  .post .listingblock,
  .post .literalblock,
  .post .sidebarblock,
  .post .verseblock,
  .post .quoteblock,
  .post .partintro,
  .post details,
  .post hr {
    margin: 1rem 0 0;
  }
  
  .post table.tableblock {
    background: var(--body-background);
    font-size: calc(15 / var(--rem-base) * 1rem);
    margin: 1.5rem 0 0;
  }
  
  @media screen and (min-width: 1024px) {
    .post table.tableblock {
      /*
      box-shadow: 5px 0 0 var(--color-white);
      */
      box-shadow: 0 0 0.25rem 0.25rem var(--color-white);
    }
  
    .post .content table.tableblock {
      box-shadow: none;
    }
  }
  
  .post table.tableblock + * {
    margin-top: 1.5rem;
  }
  
  .post p.tableblock + p.tableblock {
    margin-top: 0.5rem;
  }
  
  .post td.tableblock > .content > :first-child {
    margin-top: 0;
  }
  
  .post table.tableblock th,
  .post table.tableblock td {
    padding: 0.5rem;
  }
  
  .post table.tableblock,
  .post table.tableblock > * > tr > * {
    border: 0 solid var(--table-border-color);
  }
  
  .post table.grid-all > * > tr > * {
    border-width: 1px;
  }
  
  .post table.grid-cols > * > tr > * {
    border-width: 0 1px;
  }
  
  .post table.grid-rows > * > tr > * {
    border-width: 1px 0;
  }
  
  .post table.grid-all > thead th,
  .post table.grid-rows > thead th {
    border-bottom-width: 2.5px;
  }
  
  .post table.frame-all {
    border-width: 1px;
  }
  
  .post table.frame-ends {
    border-width: 1px 0;
  }
  
  .post table.frame-sides {
    border-width: 0 1px;
  }
  
  .post table.frame-none > colgroup + * > :first-child > *,
  .post table.frame-sides > colgroup + * > :first-child > * {
    border-top-width: 0;
  }
  
  /* NOTE let the grid win in case of frame-none */
  .post table.frame-sides > :last-child > :last-child > * {
    border-bottom-width: 0;
  }
  
  .post table.frame-none > * > tr > :first-child,
  .post table.frame-ends > * > tr > :first-child {
    border-left-width: 0;
  }
  
  .post table.frame-none > * > tr > :last-child,
  .post table.frame-ends > * > tr > :last-child {
    border-right-width: 0;
  }
  
  .post table.stripes-all > tbody > tr,
  .post table.stripes-odd > tbody > tr:nth-of-type(odd),
  .post table.stripes-even > tbody > tr:nth-of-type(even),
  .post table.stripes-hover > tbody > tr:hover {
    background: var(--table-stripe-background);
  }
  
  .post table.tableblock > tfoot {
    background: var(--table-footer-background);
  }
  
  .post .halign-left {
    text-align: left;
  }
  
  .post .halign-right {
    text-align: right;
  }
  
  .post .halign-center {
    text-align: center;
  }
  
  .post .valign-top {
    vertical-align: top;
  }
  
  .post .valign-bottom {
    vertical-align: bottom;
  }
  
  .post .valign-middle {
    vertical-align: middle;
  }
  
  .post .admonitionblock {
    margin: 1.4rem 0 0;
  }
  
  .post .admonitionblock p,
  .post .admonitionblock td.content {
    font-size: calc(16 / var(--rem-base) * 1rem);
  }
  
  .post .admonitionblock td.content > :not(.title):first-child,
  .post .admonitionblock td.content > .title + * {
    margin-top: 0;
  }
  
  .post .admonitionblock pre {
    font-size: calc(15 / var(--rem-base) * 1rem);
  }
  
  .post .admonitionblock > table {
    table-layout: fixed;
    position: relative;
    width: 100%;
    border:0;
    border-collapse: separate;
  }
  
  .post .admonitionblock td.content {
    padding: 1rem 1rem 0.75rem;
    background: var(--admonition-background);
    /*
    border: 1px solid var(--color-smoke-90);
    */
    border: 1px solid var(--color-smoke-50);
    border-radius: 0.5em;
    
    width: 100%;
    word-wrap: anywhere;
  }

  .post .admonitionblock.caution td.content {
    background: #F9EFFB;
  }

  .post .admonitionblock.tip td.content {
    background: #F1FBEE;
  }

  .post .admonitionblock.important td.content {
    background: #FFEBF1;
  }

  .post .admonitionblock.warning td.content {
    background: #FFF4EB;
  }

  .post .admonitionblock.note td.content {
    background: #EEF5FB;
  }

  
  
  .post .admonitionblock td.icon {
    position: absolute;
    top: 0;
    left: 0;
    font-size: calc(15 / var(--rem-base) * 1rem);
    line-height: 1;
    transform: translate(-0.5rem, -50%);
    background: linear-gradient(to right, rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0.2) 100%) no-repeat left center / 2.075em 100%;
    border-radius: 0.5em;
    padding: 0.25em 0.075em;
  }
  
  .post .admonitionblock td.icon i {
    display: inline-flex;
    align-items: center;
    width: auto;
    background-position-x: 0.5em;
    filter: invert(100%);
    padding-left: 2em;
    vertical-align: initial;
  }
  
  .post .admonitionblock td.icon i::after {
    content: attr(title);
    text-transform: uppercase;
    filter: invert(100%); /* NOTE reverse the filter on the icon */
    font-weight: var(--admonition-label-font-weight);
   
    font-family: var(--body-font-family);  /* FLO obligé de redéfinir ici*/
    font-style: normal;
    padding: 0 0.5em;
    margin: -0.05em; /* NOTE account for slight offset in font */
  }
  
  .post .admonitionblock.caution td.icon {
    background-color: var(--caution-color);
    color: var(--caution-on-color);
  }
  
  .post .admonitionblock.caution td.icon i {
    background-image: url(/assets/img/octicons-16.svg#view-flame);
  }
  
  .post .admonitionblock.important td.icon {
    background-color: var(--important-color);
    color: var(--important-on-color);
  }
  
  .post .admonitionblock.important td.icon i {
    background-image: url(/assets/img/octicons-16.svg#view-stop);
  }
  
  .post .admonitionblock.note td.icon {
    background-color: var(--note-color);
    color: var(--note-on-color);
  }
  
  .post .admonitionblock.note td.icon i {
    background-image: url(/assets/img/octicons-16.svg#view-info);
  }
  
  .post .admonitionblock.tip td.icon {
    background-color: var(--tip-color);
    color: var(--tip-on-color);
  }
  
  .post .admonitionblock.tip td.icon i {
    background-image: url(/assets/img/octicons-16.svg#view-light-bulb);
  }
  
  .post .admonitionblock.warning td.icon {
    background-color: var(--warning-color);
    color: var(--warning-on-color);
  }
  
  .post .admonitionblock.warning td.icon i {
    background-image: url(/assets/img/octicons-16.svg#view-alert);
  }
  
  .post .imageblock {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .post .imageblock img,
  .post .image > img {
    display: inline-block;
    height: auto;
    max-width: 100%;
    vertical-align: middle;
  }
  
  .post .image:not(.left):not(.right) > img {
    margin-top: -0.2em;
  }
  
  #preamble .abstract blockquote {
    background: var(--abstract-background);
    border-left: 5px solid var(--abstract-border-color);
    color: var(--abstract-font-color);
    font-size: calc(16 / var(--rem-base) * 1rem);
    padding: 0.75em 1em;
  }
  
  .post .quoteblock,
  .post .verseblock {
    background: var(--quote-background);
    border-left: 5px solid var(--quote-border-color);
    color: var(--quote-font-color);
  }
  
  .post .quoteblock {
    padding: 0.25rem 2rem 1.25rem;
  }
  
  .post .quoteblock .attribution {
    color: var(--quote-attribution-font-color);
    font-size: calc(15 / var(--rem-base) * 1rem);
    margin-top: 0.75rem;
  }
  
  .post .quoteblock blockquote {
    margin-top: 1rem;
  }
  
  .post .quoteblock .paragraph {
    font-style: italic;
  }
  
  .post .quoteblock cite {
    padding-left: 1em;
  }
  
  .post .verseblock {
    font-size: 1.15em;
    padding: 1rem 2rem;
  }
  
  .post .verseblock pre {
    font-family: inherit;
    font-size: inherit;
  }
  
  .post ol,
  .post ul {
    margin: 0;
    padding: 0 0 0 2rem;
  }
  
  .post ul.checklist,
  .post ul.none,
  .post ol.none,
  .post ul.no-bullet,
  .post ol.unnumbered,
  .post ul.unstyled,
  .post ol.unstyled {
    list-style-type: none;
  }
  
  .post ul.no-bullet,
  .post ol.unnumbered {
    padding-left: 1.25rem;
  }
  
  .post ul.unstyled,
  .post ol.unstyled {
    padding-left: 0;
  }
  
  .post ul.circle {
    list-style-type: square;
  }
  
  .post ul.disc {
    list-style-type: square;
  }
  
  .post ul.square {
    list-style-type: square;
  }
  
  .post ol.arabic {
    list-style-type: decimal;
  }
  
  .post ol.decimal {
    list-style-type: decimal-leading-zero;
  }
  
  .post ol.loweralpha {
    list-style-type: lower-alpha;
  }
  
  .post ol.upperalpha {
    list-style-type: upper-alpha;
  }
  
  .post ol.lowerroman {
    list-style-type: lower-roman;
  }
  
  .post ol.upperroman {
    list-style-type: upper-roman;
  }
  
  .post ol.lowergreek {
    list-style-type: lower-greek;
  }
  
  .post ul.checklist {
    padding-left: 0.5rem;
  }
  
  .post ul.checklist > li > * > i.fa-check-square-o:first-child,
  .post ul.checklist > li > * > i.fa-square-o:first-child {
    width: 1.25rem;
  }
  
  .post ul.checklist > li > * > i.fa-check-square-o:first-child {
    background-image: url(/assets/img/octicons-16.svg#view-tasklist);
  }
  
  .post ul.checklist > li > * > i.fa-square-o:first-child {
    background-image: url(/assets/img/octicons-16.svg#view-tasklist-unchecked);
  }
  
  .post .dlist .dlist,
  .post .dlist .olist,
  .post .dlist .ulist,
  .post .olist .dlist,
  .post .olist .olist,
  .post .olist .ulist,
  .post .ulist .dlist,
  .post .ulist .olist,
  .post .ulist .ulist {
    margin-top: 0.5rem;
  }
  
  .post .olist li,
  .post .ulist li {
    margin-bottom: 0.5rem;
  }
  
  .post .ulist .listingblock,
  .post .olist .listingblock,
  .post .admonitionblock .listingblock {
    padding: 0;
  }
  
  .post .admonitionblock .title,
  .post .exampleblock .title,
  .post .imageblock .title,
  .post .literalblock .title,
  .post .listingblock .title,
  .post .openblock .title,
  .post .tableblock caption {
    color: var(--caption-font-color);
    font-size: calc(16 / var(--rem-base) * 1rem);
    font-weight: var(--caption-font-weight);
    font-style: italic;
    hyphens: none;
    letter-spacing: 0.01em;
    padding-bottom: 0.075rem;
    text-align: left;
  }
  
  .post .imageblock .title {
    margin-top: 0.5rem;
    padding-bottom: 0;
  }
  
  .post details {
    margin-left: 1rem;
  }
  
  .post details > summary {
    display: block;
    position: relative;
    line-height: var(--doc-line-height);
    margin-bottom: 0.5rem;
  }
  
  .post details > summary::before {
    content: "";
    border: solid transparent;
    border-left-color: currentColor;
    border-width: 0.3em 0 0.3em 0.5em;
    position: absolute;
    top: calc((var(--doc-line-height) * 0.5 - 0.3) * 1em);
    left: -1rem;
    transform: translateX(15%);
  }
  
  .post details[open] > summary::before {
    border-color: currentColor transparent transparent;
    border-width: 0.5rem 0.3rem 0;
    transform: translateY(15%);
  }
  
  .post details > summary::after {
    content: "";
    width: 1rem;
    height: 1em;
    position: absolute;
    top: calc((var(--doc-line-height) * 0.5 - 0.5) * 1em);
    left: -1rem;
  }
  
  .post details.result {
    margin-top: 0.25rem;
  }
  
  .post details.result > summary {
    color: var(--caption-font-color);
    font-style: italic;
    margin-bottom: 0;
  }
  
  .post details.result > .content {
    margin-left: -1rem;
  }
  
  .post .exampleblock > .content,
  .post details.result > .content {
    background: var(--example-background);
    border: 0.15rem solid var(--example-border-color);
    border-radius: 0.5rem;
    padding: 0.75rem;
  }
  
  .post .exampleblock > .content::after,
  .post details.result > .content::after {
    content: "";
    display: table;
    clear: both;
  }
  
  .post .exampleblock > .content > :first-child,
  .post details > .content > :first-child {
    margin-top: 0;
  }
  
  .post .sidebarblock {
    background: var(--sidebar-background);
    border-radius: 0.75rem;
    padding: 0.75rem 1.5rem;
  }
  
  .post .sidebarblock > .content > .title {
    font-size: calc(22.5 / var(--rem-base) * 1rem);
    font-weight: var(--alt-heading-font-weight);
    line-height: 1.3;
    margin-bottom: -0.3em;
    text-align: center;
  }
  
  .post .sidebarblock > .content > :not(.title):first-child {
    margin-top: 0;
  }
  
  /* NEEDS REVIEW prevent pre in table from causing article to exceed bounds */
  .post .tableblock pre,
  .post .listingblock.wrap pre {
    white-space: pre-wrap;
  }
  
  .post pre.highlight code,
  .post .listingblock pre:not(.highlight),
  .post .literalblock pre {
    background: var(--pre-background);
    box-shadow: inset 0 0 1.75px var(--pre-border-color);
    display: block;
    overflow-x: auto;
    padding: 0.75rem;
  }
  
  
  .post pre.highlight .linenotable .linenos .linenodiv pre {
    padding: 0 .75rem 0 0;/*0.75rem;*/
    border: 0px;
    box-shadow: initial;
    color: var(--color-gray-15);
  } 

  .post pre.highlight .linenotable .code pre {
    padding: 0;
    border: 0px;
    box-shadow: initial;
  }


  .post pre.highlight {
    position: relative;
  }
  
  .post .source-toolbox {
    display: flex;
    visibility: hidden;
    position: absolute;
    top: 0.25rem;
    right: 0.5rem;
    color: var(--pre-annotation-font-color);
    font-family: var(--body-font-family);
    font-size: calc(13.5 / var(--rem-base) * 1rem);
    line-height: 1;
  }
  
  .post .listingblock:hover .source-toolbox {
    visibility: visible;
  }
  
  .post .source-toolbox .source-lang {
    text-transform: uppercase;
    letter-spacing: 0.075em;
    font-size: 0.96em;
    line-height: 1.0425;
  }
  
  .post .source-toolbox > :not(:last-child)::after {
    content: "|";
    letter-spacing: 0;
    padding: 0 1ch;
  }
  
  .post .source-toolbox .copy-button {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: transparent;
    border: none;
    color: inherit;
    outline: none;
    padding: 0;
    font-size: inherit;
    line-height: inherit;
    width: 1em;
    height: 1em;
  }
  
  .post .source-toolbox .copy-icon {
    flex: none;
    width: inherit;
    height: inherit;
  }
  
  .post .source-toolbox img.copy-icon {
    filter: var(--pre-annotation-icon-filter);
  }
  
  .post .source-toolbox svg.copy-icon {
    fill: currentColor;
  }
  
  .post .source-toolbox .copy-toast {
    flex: none;
    position: relative;
    display: inline-flex;
    justify-content: center;
    margin-top: 1em;
    background-color: var(--doc-font-color);
    border-radius: 0.25em;
    padding: 0.5em;
    color: var(--color-white);
    cursor: auto;
    opacity: 0;
    transition: opacity 0.5s ease 0.75s;
  }
  
  .post .source-toolbox .copy-toast::after {
    content: "";
    position: absolute;
    top: 0;
    width: 1em;
    height: 1em;
    border: 0.55em solid transparent;
    border-left-color: var(--doc-font-color);
    transform: rotate(-90deg) translateX(50%) translateY(50%);
    transform-origin: left;
  }
  
  .post .source-toolbox .copy-button.clicked .copy-toast {
    opacity: 1;
    transition: none;
  }
  
